import {commonParams} from './config'
import axios from 'axios'

export function getLyric(songMid) {
  const url = '/api/lrc'

  const data = Object.assign({}, commonParams, {
    platform: 'yqq', // 
    hostUin: 0,
    needNewCode: 0,
    loginUin: 0,
    format: 'json', // json
    pcachetime: +new Date(), // 
    songmid: songMid
  })

  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}



